import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { LoginInfo, RedP } from "./Movies.style";

export default function Loginpage() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [isEmailValid, setIsEmailValid] = useState(true);
  const [pw, setPW] = useState("");
  const [isPWValid, setIsPWValid] = useState(true);

  const handleHome = () => {
    //      
    if (isEmailValid && isPWValid) {
      navigate("/Home");
    }
  };

  const EmailChange = (e) => {
    const inputValue = e.target.value;
    setEmail(inputValue);

    //   
    const isValid = /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(inputValue);
    setIsEmailValid(isValid);
  };

  const PWChange = (e) => {
    const inputValue = e.target.value;
    setPW(inputValue);

    const isPWValid =
      /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/.test(
        inputValue
      );
    setIsPWValid(isPWValid);
  };

  return (
    <LoginInfo>
      <h2>  </h2>
      <p> </p>
      <input type="text" value={email} onChange={EmailChange} />
      {isEmailValid ? (
        <RedP>&nbsp;</RedP>
      ) : (
        <RedP>  .</RedP>
      )}
      <p></p>
      <input type="password" value={pw} onChange={PWChange} />
      {isPWValid ? (
        <RedP>&nbsp;</RedP>
      ) : (
        <RedP>,,  8  .</RedP>
      )}
      <button onClick={handleHome}></button>
    </LoginInfo>
  );
}
