import React from "react";
import { useNavigate } from "react-router-dom";
import { NotFoundInfo } from "./Movies.style";

export default function NotFound() {
  const navigate = useNavigate();

  const handleHome = () => {
    navigate("/Home");
  };
  return (
    <NotFoundInfo>
      <h2>   .</h2>
      <p>      .</p>
      <button onClick={handleHome}>  </button>
    </NotFoundInfo>
  );
}
