import React, { Component } from "react";
import Ad from "./Ad";

class AdPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showAd: true, //    true 
    };
  }

  handleToggleClick = () => {
    this.setState((prevState) => ({
      showAd: !prevState.showAd, // showAd    show/hide
    }));
  };

  render() {
    return (
      <div>
        <Ad showAd={this.state.showAd} />
        {/*     handleToggleClick    . */}
        <button onClick={this.handleToggleClick}>
          {this.state.showAd ? "" : ""}
        </button>
      </div>
    );
  }
}

export default AdPage;
